<template>
  <div id="app">
    <header>
      <img src="img/sngtv.png" alt="">
      <button type="button" name="btn-menu" class="btn fr" @click="showMenu=!showMenu; reloadLinks()" v-if="me !== undefined">
        <i :class="`fa ${showMenu?'fa-remove':'fa-bars'} fa-lg`"></i></button>
    </header>
    <nav :class="showMenu?'enabled':''" @click="showMenu = false">
      <!-- <a href="/#/" class="btn btn-base4 txt-l"><i class="fa fa-home fa-lg"></i>&nbsp;Accueil</a>
      <a href="/#/parameters" class="btn btn-base4 txt-l"><i class="fa fa-gear fa-lg"></i>&nbsp;Paramtres</a>
      <a href="/#/login" class="btn btn-danger txt-l" @click="disconnect()" v-if="id_mobile"><i class="fa fa-sign-out fa-lg"></i>&nbsp;se dconnecter</a> -->
      
      <router-link to="/home" class="btn btn-base4 txt-l"><i class="fa fa-home fa-lg"></i>&nbsp;Accueil</router-link>
      <router-link to="/segmentation" class="btn btn-base3 txt-l"><i class="fa fa-user fa-lg"></i>&nbsp;Vous</router-link>
      <a href="#" @click="externalLink(l.URL)" class="btn btn-base3 txt-l" v-for="l in links"><i class="fa fa-question fa-lg"></i>&nbsp;{{l.title}}</a>
      <!-- <a target="_blank" href="#" @click="externalLink(l.URL)" class="btn btn-base3 txt-l" v-for="l in links"><i class="fa fa-question fa-lg"></i>&nbsp;{{l.title}}</a> -->
      <!-- TODO -->
      <!-- <div class="input-group">
        <input type="number" name="" value="" placeholder="num export" v-model="num_export" class="col-12">
      </div>
      <router-link :to="`/home?num_export=${num_export}`" class="btn btn-base3 txt-l">update from {{last_num_export}} to {{num_export}}</router-link> -->
      
      <!-- <router-link to="parameters" class="btn btn-base4 txt-l"><i class="fa fa-gear fa-lg"></i>&nbsp;Paramtres</router-link> -->
      <!-- <router-link to="/login" class="btn btn-danger txt-l" @click="disconnect()" v-if="id_mobile"><i class="fa fa-sign-out fa-lg"></i>&nbsp;se dconnecter</router-link> -->
      <button class="btn btn-danger txt-l" style="position: absolute;bottom:60px;right:0px;" @click="disconnect()" v-if="id_mobile"><i class="fa fa-sign-out fa-lg"></i>&nbsp;se dconnecter</button>
    </nav>
    <main>
      <router-view/>
    </main>
    <footer>
      GTV  2018
    </footer>
    <div class="messenger fix-tl" v-if="messages.length > 0">
      <div :class="`messenger-content ${msg.type}`" v-for="(msg, k) in messages" @click="messages.splice(k, 1)">
        <div class="icon"><i :class="`fa ${msg.icon}`"></i></div>
        <div class="message">{{msg.message}}</div>
      </div>
    </div>
    <div class="wait" v-if="wait.c>0">
      <div class="txt-c">
        <i class="fa fa-4x fa-cog fa-spin"></i><br>
        <span>{{wait.msg || 'Veuillez patienter'}}</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      accessToken: window.gtvapp.apiOptions.Authorization,
      id_mobile: window.gtvapp.id_mobile,
      me: window.gtvapp.me,
      messages: window.gtvapp.messages,
      wait: window.gtvapp.wait,
      waitmsg: window.gtvapp.waitmsg,
      showMenu: false,
      last_num_export: window.gtvapp.num_export,
      c: 0,
      num_export: undefined,
      links: window.gtvapp.links
    }
  },
  methods: {
    reloadLinks () {
      console.log('reloadLinks')
      this.links = window.gtvapp.links
      console.log(JSON.stringify(this.links))
    },
    externalLink (link) {
      if ((typeof cordova) !== 'undefined') {
        cordova.InAppBrowser.open(`http://195.221.21.143:8082${link}`, '_blank', 'location=no,closebuttoncaption= revenir,transitionstyle=crossdissolve,toolbarposition=bottom')
      } else {
        window.open(`http://195.221.21.143:8082${link}`, '_system')
      }
    },
    disconnect () {
      console.log('============================================ disconnect')
      // window.localStorage.removeItem('accessToken')
      // window.gtvapp.apiOptions.Authorization = undefined
      // this.accessToken = undefined
      window.gtvapp.num_export = undefined
      window.localStorage.removeItem('num_export')
      window.gtvapp.id_mobile = undefined
      window.localStorage.removeItem('id_mobile')
      this.id_mobile = undefined
      window.localStorage.removeItem('me')
      window.gtvapp.me = undefined
      this.me = undefined
      window.gtvapp.categories = []
      window.gtvapp.docsets = []
      const sqldb = window.openDatabase('gtvappdb', '1.0', 'GTVAPP database', 1000000)
      sqldb.transaction(
        (tx) => {
          tx.executeSql('DROP TABLE IF EXISTS Categories')
          tx.executeSql('DROP TABLE IF EXISTS DocSets')
          tx.executeSql('DROP TABLE IF EXISTS Pdf')
        },
        (err) => {
          console.log('SQL ERROR : ' + err.code)
          this.$router.push('/login')
        },
        () => {
          console.log('BDD vide avec succs')
          this.$router.push('/login')
        }
      )
    }
  },
  created () {
    console.log(JSON.stringify({wstoken: window.localStorage.getItem('id_mobile'), me: JSON.parse(window.localStorage.getItem('me'))}))
    // this.disconnect()
    let wstoken = window.localStorage.getItem('id_mobile')
    let me = JSON.parse(window.localStorage.getItem('me'))
    console.log('App.vue:116')
    console.log(JSON.stringify({wstoken: wstoken, me: me}))
    if (wstoken === null || wstoken === undefined || me === null || me === undefined) {
      console.log('App.vue:119  /login')
      this.$router.push('/login')
    }
    else {
      window.gtvapp.id_mobile = wstoken
      this.id_mobile = wstoken
      window.gtvapp.me = me
      this.me = me
      this.$router.push('/')
      // this.$router.push('/segmentation')
    }
    console.log(`user : ${JSON.stringify(this.me)}`)
    console.log(`num_export : ${JSON.stringify(window.gtvapp.num_export)}`)
    
    let router = this.$router
    window.gtvapp.push.on('notification', function(data) {
      console.log('notification event')
      console.log(JSON.stringify(data))
      if (device.platform === 'Android') {
        navigator.notification.alert(
          data.message,         // message
          function() {
            console.log(window.location.pathname)
            console.log(data.additionalData.path)
            // window.localStorage.setItem('pushpath', data.additionalData.path)
            router.push(data.additionalData.path)
          },
          data.title,
          'En savoir plus'
        )
      } else {
        navigator.notification.alert(
          // JSON.stringify(data),         // message
          data.message,         // message
          function() {
            // console.log(window.location.pathname)
            // console.log(data.additionalData.data.path)
            // // window.localStorage.setItem('pushpath', data.additionalData.path)
            router.push(data.additionalData.path)
          },
          data.title,
          // 'title TODO',
          'En savoir plus'
        )
      }
    })
  },
  updated () {
    console.log(this.$route.path)
    // console.log('App.vue updated')
    // let wstoken = window.localStorage.getItem('id_mobile')
    // let me = JSON.parse(window.localStorage.getItem('me'))
    // console.log('App.vue:137')
    // console.log(JSON.stringify({wstoken: wstoken, me: me}))
    // if (wstoken === null || wstoken === undefined || me === null || me === undefined) {
    //   console.log('App.vue:140  /login')
    //   // this.$router.push('/login')
    // }
    // else {
    //   window.gtvapp.id_mobile = wstoken
    //   this.id_mobile = wstoken
    //   window.gtvapp.me = me
    //   this.me = me
    // }
  }
}
</script>

<style lang="less"> @import './styles/importer.less'; </style>



