export default {
  name: 'Login',
  data () {
    return {
      db: window.gtvapp.db,
      authok: false,
      // authmsg: 'not auth',
      form: {
        email: undefined,
        password: undefined,
      }
    }
  },
  methods: {
    login () {
      // window.gtvapp.wait.c++
      this.$http.get(
        `${window.gtvapp.interfaceUrl}/Identification/${this.form.email}/${this.form.password}`/*,
        {headers:window.gtvapp.interfaceOptions}
      */).then(
        res => {
          if(res.body.data !== undefined && res.body.data.message_ws==="IDENTIFICATION VALIDE"){
            console.log(JSON.stringify(res))
            console.log(JSON.stringify({id_mobile: res.body.data.id_mobile, me: this.form.email}))
            // this.$ls.set('id_mobile', res.body.data.id_mobile)
            // this.$ls.set('me', JSON.stringify({login: this.form.email}))
            window.localStorage.setItem('id_mobile', res.body.data.id_mobile)
            window.localStorage.setItem('me', JSON.stringify({login: this.form.email}))
            // await this.db.putSQL('User', {_id: id_mobile, data: res.body.data.id_mobile})
            // await this.db.putSQL('User', {_id: me, data: this.form.email})
            window.gtvapp.interfaceOptions.Authorization = res.body.data.id_mobile
            window.gtvapp.id_mobile = res.body.data.id_mobile
            window.gtvapp.me = {login: this.form.email}
            window.gtvapp.messages.push({type:'success', message: 'Bienvenue'})
            setInterval(()=>{window.gtvapp.messages.pop()}, 5000)
            console.log('identification ok')
            console.log(JSON.stringify(window.gtvapp))
            this.authok = true
            // this.authmsg = "AUTH SUCCESS !!"
            // window.gtvapp.wait.c--
            // this.$router.push('/segmentation')
          } else {
            // window.gtvapp.wait.c--
            console.log('identification ko')
            this.authok = true
            // this.authmsg = "AUTH FAILED !!"
            // window.gtvapp.messages.push({type:'danger', message: res.body.data.message_ws})
          }
          this.$forceUpdate()
        },
        err => {
          console.log(JSON.stringify(err))
          // window.gtvapp.wait.c--
          window.gtvapp.messages.push({type:'danger', message: `${JSON.stringify(err)}`})
        }
      )
    }
    // login () {
    //   this.form.strategy = 'local'
    //   window.gtvapp.wait.c++
    //   this.$http.post(
    //     `${window.gtvapp.apiUrl}/authentication`,
    //     this.form,
    //     {headers:window.gtvapp.apiOptions}
    // 
    //   ).then(
    //     res => {
    //       this.$ls.set('accessToken', res.body.accessToken)
    //       window.gtvapp.apiOptions.Authorization = res.body.accessToken
    //       window.gtvapp.interfaceOptions.Authorization = res.body.id_mobile
    //       window.gtvapp.me = res.body.user
    //       this.$ls.set('me', JSON.stringify(res.body.user))
    //       this.$ls.set('acessToken', JSON.stringify(res.body.accessToken))
    //       this.$ls.set('id_mobile', res.body.id_mobile)
    //       window.location = '/#/'
    //       window.gtvapp.wait.c--
    //       window.gtvapp.messages.push({type:'success', message: 'Bienvenue'})
    //       setInterval(()=>{window.gtvapp.messages.pop()}, 5000)
    //     },
    //     err => {
    //       window.gtvapp.wait.c--
    //       window.gtvapp.messages.push({type:'danger', message: `${err.body.code} : ${err.body.message}`})
    //     }
    //   )
    // }
  },
  created () {
    console.log('login vue created')
  },
  mounted () {
    console.log('login vue mounted')
  },
  beforeDestroy () {
    console.log('login vue will be destroyed')
  },
  destroyed () {
    console.log('login vue destroyed')
  },
  beforeRouteUpdate (to, from, next) {
    console.log('beforeRouteUpdate')
    console.log(JSON.stringify({to: to, from: from}))
    next()
  }
}


