import pdf from 'vue-pdf'
import latinize from 'latinize'

export default {
  name: 'Docset',
  data () {
    return {
      docsetId: this.$route.params.docset,
      pdfId: this.$route.params.pdf,
      docsets: window.gtvapp.docsets,
      docSet: undefined,
      pj: undefined,
      pdfFile: undefined,
      page: 1,
      numPages: 0,
      loaded: false,
      db: window.gtvapp.db,
      latinize: latinize
    }
  },
  components: {
    pdf
  },
  methods: {
    getDoc (id) {
      console.log(`${window.gtvapp.interfaceUrl}/PDF/${id}/${window.gtvapp.id_mobile}`)
    },
    range(s, e) {
      let r = []
      if(e!==undefined){
        for(let i = s; i <= e; i++){
          r.push(i)
        }
      }
      return r
    },
    b64toBlob (b64Data, contentType, sliceSize) {
      contentType = contentType || ''
      sliceSize = sliceSize || 512
      let byteCharacters = atob(b64Data)
      let byteArrays = []
      for (let offset = 0; offset < byteCharacters.length; offset += sliceSize) {
        let slice = byteCharacters.slice(offset, offset + sliceSize)
        let byteNumbers = new Array(slice.length)
        for (let i = 0; i < slice.length; i++) {
          byteNumbers[i] = slice.charCodeAt(i)
        }
        let byteArray = new Uint8Array(byteNumbers)
        byteArrays.push(byteArray)
      }
      var blob = new Blob(byteArrays, {type: contentType})
      return blob
    },
    savebase64AsPDF (folderpath,filename,content,contentType) {
      // Convert the base64 string in a Blob
      let DataBlob = this.b64toBlob(content,contentType)
      console.log('Starting to write the file :3')
      
      window.resolveLocalFileSystemURL(folderpath, function (dir) {
        console.log('Access to the directory granted succesfully')
        dir.getFile(filename, { create: true }, function (file) {
          console.log('File created succesfully.')
          file.createWriter(function (fileWriter) {
            console.log('Writing content to file')
            fileWriter.write(DataBlob)
          }, function () {
            console.log('Unable to save file in path '+ folderpath)
          })
        })
      })
    },
    downloadFile (f64, fname) {
      console.log(fname)
      let contentType = 'application/pdf'
      try {
        let folderpath = cordova.file.externalDataDirectory
        console.log('cordova available, lets write the file system')
        console.log(folderpath)
        if(folderpath === null) folderpath = 'file:///storage/emulated/0/'
        console.log(folderpath)
        let filename = `${fname}.pdf`
        this.savebase64AsPDF(folderpath, filename, f64, contentType)
      } catch (e) {
        console.log(JSON.stringify(e))
        console.log('cordova unavailable, we have to bricole !')
        window.open(`data:application/octet-stream;base64,${f64}`)
      }
    }
  },
  async mounted () {
    let id = this.$route.params.docset
    this.docSet = await this.db.getSQL('DocSets', id)
    // this.docSet = await this.db.get('DocSets', id)
    // console.log(JSON.stringify({docSet: this.docSet}))
    // if(id !== undefined){
    //   this.docSet = this._.find(this.docsets, function(o){return o._id == id})
    // } else {
    //   this.docSet = undefined
    // }
    let pdfId = this.$route.params.pdf
    // this.pj = await this.db.get('Pdf', pdfId)
    if(pdfId !== undefined){
      this.pj = this._.find(this.docSet.docs, function(o){return o.code_pj == pdfId})
    } else {
      this.pj = undefined
    }
    // console.log(JSON.stringify({'this.pj': this.pj}))
    const idb = window.indexedDB
    let open = idb.open('gtvappdb', 1)
    let locpdf = await this.db.getSQL('Pdf', this.pj.code_pj)
    // let locpdf = await this.db.get('Pdf', this.pj.code_pj)
    // console.log(JSON.stringify({'pdf': pdf}))
    // console.log(pdf === undefined)
    if(locpdf === undefined){
      if(window.gtvapp.wait.c===0){
        // console.log('get the PDF')
        window.gtvapp.wait.c++
        this.$http.get(
          `${window.gtvapp.interfaceUrl}/Fichier/${this.$route.params.pdf}/${window.gtvapp.id_mobile}`/*,
          {headers:window.gtvapp.interfaceOptions}*/
        ).then(
          res => {
            window.gtvapp.wait.c--
            this.pdfFile = res.body.data.fichier_xml
          }
        )
      }
    } else {
      console.log('pdf synchronized')
      this.pdfFile = locpdf.content
    }
  }
}


