export default {
  name: 'Category',
  data () {
    return {
      categories : window.gtvapp.categories,
      currentCategories: [],
      docsets: window.gtvapp.docsets,
      db: window.gtvapp.db,
      current: undefined,
      md: new window.utils.md(),
      search: {
        query: undefined
      },
      links: [],
      page: 0,
      pageSize: 10,
      showResume: undefined,
      curuniv: undefined
    }
  },
  methods: {
    stopPropagation (event) {
      console.log('stop propagation')
      event.stopPropagation()
    },
    range (s, e) {
      let ret = []
      for (let i = 0; i <= e; i++) {
        ret.push(i)
      }
      return ret
    },
    async rootCat (id) {
      if(this.categories == undefined) {
        try {
          window.gtvapp.wait.c++
          window.gtvapp.wait.msg = 'Chargement des categories...'
          this.categories = await this.db.getSQL('Categories')
          window.gtvapp.wait.c--
        } catch (ex) {
          console.log(JSON.stringify(ex))
        }
        // this.categories = await this.db.get('Categories')
      }
      let ret
      if(this.categories !== undefined){
        let curuniv = this.categories.filter(function(cat) {
          return cat._id == id
        })
        if (curuniv.length > 0 && curuniv[0].level === 0) {
          window.gtvapp.curuniv = curuniv[0]
        }
        this.curuniv = window.gtvapp.curuniv
        ret = this.categories.filter(function(cat) {
          return cat.parent == id
        })
      } else {
        ret = []
      }
      return ret
    },
    async getDocSet(id) {
      let res
      try {
        window.gtvapp.wait.c++
        window.gtvapp.wait.msg = 'Chargement du document...'
        res = await this.db.getSQL('DocSets', parseInt(id))
        window.gtvapp.wait.c--
      } catch (ex) {
        console.log(JSON.stringify(ex))
      }
      return res
      // return await this.db.get('DocSets', parseInt(id))
    },
    async getContent () {
      let root = this.$route.params.id
      try {
        window.gtvapp.wait.c++
        window.gtvapp.wait.msg = 'Chargement des categories...'
        this.currentCategories = await this.rootCat(root)
        this.currentCategories = this.currentCategories.sort((a, b) => {
          if (a.level === 0 && b.level === 0) {
            return a._id < b._id ? -1 : a._id > b._id ? 1 : 0
          } else {
            return a.name < b.name ? -1 : a.name > b.name ? 1 : 0
          }
          
        })
        window.gtvapp.wait.c--
      } catch (ex) { console.log(JSON.stringify(ex)) }
      if(root !== undefined && root !== 'root'){
        try {
          window.gtvapp.wait.c++
          window.gtvapp.wait.msg = 'Chargement de la categories...'
          this.current = await this.db.getSQL('Categories', parseInt(root))
          window.gtvapp.wait.c--
        } catch (ex) {
          console.log(JSON.stringify(ex))
        }
        // this.current = await this.db.get('Categories', parseInt(root))
        if(this.current !== undefined && (this.current.child===undefined || this.current.child.length==0) && this.current.docsets.length>0) {
          this.current.popDocsets = []
          let l = this.current.docsets.length
          
          // this.current.docsets.forEach(async (id) => {
          for (let i = this.page * this.pageSize; i < (this.page + 1) * this.pageSize; i++) {
            let id = this.current.docsets[i]
            if (id !== undefined) {
              try {
                window.gtvapp.wait.c++
                window.gtvapp.wait.msg = `Chargement des documents`
                let d = await this.db.getSQL('DocSets', id)
                console.log(id)
                this.current.popDocsets.push(d)
                // c++
                window.gtvapp.wait.c--
              } catch (ex) {
                console.log(JSON.stringify(ex))
              }
            }
            this.$forceUpdate()
            // let d = await this.db.get('DocSets', id)
          }
        }
      } else {
        this.current = undefined
      }
      this.$forceUpdate()
    }
  },
  watch: {
    '$route': async function () {
      this.getContent()
    },
    'page': async function () {
      this.getContent()
    }
  },
  async mounted () {
    this.getContent()
  }
}


