export default {
  name: 'Parameters',
  data () {
    return {
      me: window.gtvapp.me
    }
  },
  methods: {
    save () {
      window.gtvapp.wait.c++
      this.$http.patch(
        `${window.gtvapp.apiUrl}/users/${this.me._id}`,
        this.me,
        {headers:window.gtvapp.apiOptions}
      ).then(
        res => {
          window.gtvapp.wait.c--
          window.gtvapp.messages.push({type:'success', message: 'Modifications enregistres avec succs'})
          setInterval(()=>{window.gtvapp.messages.pop()}, 5000)
        },
        err => {
          window.gtvapp.wait.c--
          window.gtvapp.messages.push({type:'danger', message: `${err.body.code} : ${err.body.message}`})
        }
      )
    }
  },
  created () {
    this.me = window.gtvapp.me
  }
}


