export default {
  data () {
    return {
      questions : [],
      ready: false
    }
  },
  methods: {
    isReady () {
      let rep = true
      this.questions.forEach(q => {
        rep = rep && (q.answer !== undefined)
      })
      this.ready = rep
    },
    getQuestions () {
      window.gtvapp.wait.c++
      window.gtvapp.wait.msg = 'Chargement du contenu...'
      this.$http.get(
        `${window.gtvapp.interfaceUrl}/Segmentation/${window.gtvapp.id_mobile}`/*,
        {headers:window.gtvapp.interfaceOptions}
      */).then(
        res => {
          // console.log(JSON.stringify(res))
          try {
            this.questions = res.body.Questions.liste.sort((a, b) => a.ordre < b.ordre ? -1 : a.ordre > b.ordre ? 1 : 0)
          } catch (e) {
            console.log(JSON.stringify(e))
          }
          window.gtvapp.wait.c--
        },
        err => {
          console.log(JSON.stringify(err))
          window.gtvapp.wait.c--
          window.gtvapp.messages.push({type:'danger', message: `${err.body.code} : ${err.body.message}`})
        }
      )
    },
    answer () {
      window.gtvapp.wait.c++
      let segmentation = []
      this.questions.forEach(q => {
        let answer = {
          question: q._id,
          answer: q.answer
        }
        segmentation.push([q._id, q.answer])
      })
      this.$http.get(
        `${window.gtvapp.interfaceUrl}/SegmentationAnswer/${window.gtvapp.id_mobile}/${JSON.stringify(segmentation)}`
      ).then(
        res => {
          window.gtvapp.wait.c--
          this.$router.push('/')
        },
        err => {
          console.log(JSON.stringify(err))
          window.gtvapp.wait.c--
          window.gtvapp.messages.push({type:'danger', message: `${err.body.code} : ${err.body.message}`})
        }
      )
      console.log(JSON.stringify(segmentation))
    }
  },
  mounted () {
    console.log('segmentation vue mounted')
    this.getQuestions()
  },
  created () {
    console.log('segmentation vue created')
  },
  beforeDestroy () {
    console.log('segmentation vue will be destroyed')
  },
  destroyed () {
    console.log('segmentation vue destroyed')
  }
}


