export default {
  data () {
    return {
      categories : window.gtvapp.categories,
      currentCategories: [],
      docsets: window.gtvapp.docsets,
      db: window.gtvapp.db,
      current: undefined,
      md: new window.utils.md(),
      search: {
        query: undefined
      },
      searchResults: [],
      visible: undefined,
      cond: {}
    }
  },
  methods: {
    async rootCat (id) {
      if(this.categories == undefined) {
        this.categories = await this.db.get('Categories')
      }
      let ret
      if(this.categories !== undefined){
        ret = this.categories.filter(function(cat) {
          return cat.parent == id
        })
      } else {
        ret = []
      }
      return ret
    },
    getDocSet(id) {
      return this._.find(this.docsets, {_id: id})
    },
    cmpKws (kws) {
      let cmpKws = (
        kws !== undefined && kws !== '' 
        ? kws.split(',').map((k) => {return k.trim().toLowerCase()})/*.join(' ').split(' ').map((k) => {return k.trim().toLowerCase()})*/
        : []
      )
      return cmpKws
    },
    async handleRouteQuery (req) {
      console.log(req.q)
      this.search.query = req.q
      window.gtvapp.wait.c++
      window.gtvapp.wait.msg = 'Recherche en cours...'
      this.cond.cat = req.cat
      this.searchResults = await this.db.search('DocSets', req.q, this.cond)
      window.gtvapp.wait.msg = undefined
      window.gtvapp.wait.c--
      this.$forceUpdate()
    }
  },
  watch: {
    '$route': async function () {
      // SEARCH ENGINE
      if (this.$route.query.q) this.handleRouteQuery(this.$route.query)
    }
  },
  async mounted () {
    // SEARCH ENGINE
    if (this.$route.query.q) this.handleRouteQuery(this.$route.query)
  }
}


