import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Login from '@/components/Login'
import Parameters from '@/components/Parameters'
import Category from '@/components/Category'
import DocSet from '@/components/Docset'
import Pdf from '@/components/Pdf'
import Search from '@/components/Search'
import Segmentation from '@/components/Segmentation'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/segmentation',
      name: 'Segmentation',
      component: Segmentation
    },
    {
      path: '/home',
      name: 'Home',
      component: Home
    },
    {
      path: '/category',
      name: 'Category',
      component: Category
    },
    {
      path: '/category/:id',
      name: 'Category',
      component: Category
    },
    {
      path: '/docset/:id',
      name: 'DocSet',
      component: DocSet
    },
    {
      path: '/search',
      name: 'Search',
      component: Search
    },
    {
      path: '/pdf/:docset/:pdf',
      name: 'Pdf',
      component: Pdf
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/parameters',
      name: 'Parameters',
      component: Parameters
    }
  ]
})



